import React from 'react';
import { connect } from 'react-redux';
import { actions } from './store';
import reactMixin from 'react-mixin';
import mixins from '../../mixins/index';
import style from './style.css';
import { serverWithStyle } from '@/highOrderComponents';

@connect(
  state => ({ list: state.homeReducer.list }),
  actions
)
@reactMixin.decorate(mixins())
// 
@serverWithStyle(style)
class Home extends React.Component {
  constructor(props) {
    super(props);
  }
  // 
  componentDidMount() {
    // 
    if (this.props.list.length === 0) {
      this.props.getHomeList();
    }
  }
  render() {
    return (
      <div className={style.Home}>
        {
          this.props.list.map((item, index) => (<p key={index}>{item.title}</p>))
        }
        <img src={ require('../../assets/images/bg.jpeg') }  alt=""></img>
      </div>
    );
  }
}

// https://reacttraining.com/react-router/web/guides/server-rendering
Home.loadData = (store) => {
  // 
  // action   Promise  Promise.all 
  return store.dispatch(actions.getHomeList());
}

export default Home;
